import React, { useState } from 'react';
//CSS
import './qualification.scss';
//  Icons
import next from '../../resources/svg/next.svg';


export default function Qualification({title, institution, industry, start, end, majors, status, progress, nqf}) 
{
  const [index, setIndex] = useState(0);

  return (
    <div className="qualification">
      <h5 className="qualification-title">
        {title}
      </h5>
      <div className="qualification-institution">
      <span>Institution:</span>  <span>{institution}</span>
      </div>
      <div className="qualification-industry">
        <span>Industry:</span>
        <span>
          {
            industry === undefined ? 'N/A' : `${industry}`
          }
          </span>
      </div>
      <div className="qualificaton-enrollment">
        <span>Enrollment:</span>
        <span>{start} - {end}</span>
      </div>
      <div className="qualification-majors">
        <span>Majors :</span>
      </div>
      <div className = "qualification-major">
        <img src={next} alt="next" className = "ztp next" onClick = {e => setIndex(() => (index + 1) % majors.length)}/>
        <span className="ztp"><span className="count">{index + 1}</span> {majors[index]}</span> 
      </div>
      <div className="qualification-status">
      <span>Status:</span>
        <span className = {(status ? 'status complete' : 'status enrolled')}>
          {status ? 'Complete' : 'Enrolled'} - {(progress === undefined && status === true) ? '100%' : progress}
        </span>
      </div>
      <div className="qualification-nqf">
        <span>NQF Level:  </span>
        <span>
          {
          (typeof nqf === 'number') ? <span className="nqf">{nqf}</span> : 'N/A' 
          }
          </span>
      </div>
    </div>
  )
}

