import React from 'react'

export default function AboutMe({heading})
{
  return (
    <>
      <h4 className = "heading">{heading}</h4>
      <p className = "about">
        An accountant by profession so i understand business problems, profit margins and the fundamental principle that any business tool is only as valuable as the value it creates.  A programmer at heart so i enjoy the process of putting things together, thinking outside the box and solving problems in creative and innovative ways. I'm all about providing  brand based business solutions by creating innovative and modern web based tools . I believe that all successful business are established on a foundation of solid branding, so all my projects are undertaken with with this fundamental foundation in mind. The key to creating a brand that resonates with consumers on a deep level lies in making your enterprise a reflection of your individuality and taking that premise as far as possible.
      </p>
    </>
  )
}
