import React from 'react';
//Components
import Qualification from '../qualifications/Qualification.jsx';


export default function Education({heading})
{
  const matric = ['English', 'Mathematics', 'Accounting',  'Business Studies', 'Computer Applications Technology (CAT)'];
  const paralegal = ['Legal Processes, Constitutional  & Administrative Law', 'Alternative Dispute Resolution (ADR)', 'Legal Research,  Writing & Interpretation', 'Family Law and Divorce', 'Wills and Estates'];
  const bacc = ['Financial Accounting', 'Taxation', 'Auditing', 'Management Accounting', 'Financial Management', 'Company Law & Practice'];

  return (
    <>
     <h4 className = "heading">{heading}</h4>
     <div className="education">
       <Qualification
          title = 'Bachelors of Accounting [Honors]'
          institution = 'University of Namibia'
          industry = 'Finance'
          start = '2016'
          end = 'Present'
          majors = {bacc}
          status = {false}
          progress = '96%'
          nqf = {8}
       />
       <Qualification
          title = 'Diploma in Paralegal Studies'
          institution = 'University of Namibia'
          industry = 'Legal'
          start = '2013'
          end = '2015'
          majors = {paralegal}
          status = {true}

          nqf = {5}
       />
       <Qualification
          title = 'Matric [NSC - SA]'
          institution = 'Groote Schuur High School'
          start = '2007'
          end = '2011'
          majors = {bacc}
          majors = {matric}
          status = {true}
          nqf = {3}
       />
     </div>
    </>
  )
}
