import React from 'react'

export default function Philosophy({heading})
{
  return (
    <>
      <h4 className = "heading">{heading}</h4>
      <p className="about">
          Professional philosophy : 
      </p>
        <ol>
              <li><span>1</span>Clients vision</li>
              <li><span>2</span>Clients brand</li>
              <li><span>3</span>Practicality</li>
              <li><span>4</span>Foresight</li>
              <li><span>5</span>Uniqueness</li>
              <li><span>6</span>End user</li>
              <li><span>7</span>Efficiency</li>
              <li><span>8</span>Effectiveness</li>
              <li><span>9</span>Quality</li>
          </ol>   
    </>
  )
}
