//React
import React from 'react';
//Styles
import './rating.scss';

export default function Rating({field, darkgrey, lightGrey}) 
{
  return (
      <div className="rating">     
        <div className = "rating__field">{field}:</div>
        <div className="rating__ratings ztp">
          {
            Array(darkgrey).fill().map((_, i) =>
            (
              <span key = {`${field} ${i}`} className="rating-bullet rating__darkGrey"></span>
            ))
          }
          {
            Array(lightGrey).fill().map((_, i) =>
            (
              <span  key = {`${field}${i}${i}`} className="rating-bullet rating__lightGrey"></span>
            ))
          }
        </div>
      </div>
  )
}
