import React from 'react';
//styles
import './portfolioSidebar.scss';
//images
import Me from '../../../../../../resources/images/IMG_2019.JPG';
//Icons
import Email from '../../resources/svg/Email-595b40b65ba036ed117d43e6.svg';
import LinkedIn from '../../resources/svg/iconfinder_LinkedinIcon_5584534.svg';
import Twitter from '../../resources/svg/iconfinder_twitter_5584539.svg';
import Facebook from '../../resources/svg/icons8-facebook (1).svg';
import Instagram from '../../resources/svg/Instagram-595b40b65ba036ed117d4472.svg';
import Contact from '../../resources/svg/telephone-receiver-with-circular-arrows-svgrepo-com.svg';
import Github from '../../resources/svg/icons8-github.svg';

export default function PortfolioSidebar() 
{
  return (
    <div className = "portfolio-sidebar">
      
      <div className="portfolio-sidebar__image spc-container">
        <img             
            src={Me} alt=""/>
      </div>

      <h1 className="portfolio-sidebar__name text-center">
        Mulatiwa Mwambwa
      </h1>

      <div className="portfolio-sidebar__slogan text-center">
        "It''s all about Branding !!!"
      </div>

      <div className="portfolio-sidebar__title text-center">
        <ul>
          <li>Accountant</li>
          <li>Web developer</li>
          <li>Paralegal</li>
        </ul>
      </div>
        

      <div className="portfolio-sidebar__details">        
        <div className="portfolio-sidebar__details__contactDetails">
          <div className="findMe">Find me <span className="at">@</span> </div>
          <div className="social-container">
          <div className="social">
            <a href="mailto:mulatiwa.mwa@gmail.com">
            <img src={Email} alt="email"/>
            </a>
          </div>          
          <div className="social">
            <a href="tel:+264813056002">
              <img src= {Contact} alt="contact number"/>
            </a>
          </div>          
          <div className="social">
            <a href="https://www.facebook.com/mulatiwaYaka">
              <img src={Facebook} alt="Facebook"/>
            </a>
          </div>          
          <div className="social">
            <a href="https://www.instagram.com/mulatiwayaka/">
              <img src={Instagram} alt="Instagram"/>
            </a>
          </div>          
          <div className="social">
            <a href="linkedin.com/in/mulatiwa-mwambwa-179010138">
              <img src={LinkedIn} alt="LinkedIn"/>
            </a>
          </div>          
          <div className="social">
            <a href="https://twitter.com/mulatiwa">
              <img src={Twitter} alt="Twitter"/>
            </a>
          </div>          
          <div className="social">
            <a href="https://github.com/mulatiwa">
              <img src= {Github} alt=""/>
            </a>
          </div> 
          </div>         
        </div>
      </div>
    </div>
  )
}
